<template>
    <div class="managerPanoramicNew">
        <div class="top">
            <div class="content content-border">
                <div class="content-title">
                    <span class="block"></span>
                    <span>关注标签</span>
                </div>
                <div class="chart-main">
                    <div class="nav"></div>
                    <div class="tagContent" v-loading="tagLoading">
                        <div v-for="item in tagListArray" :key="item.tagId">
                            <div class="tagItem" >
                                <div class="tagTypeCon">
                                    <div class="tagType" :class="tagObj(item.tagRuleType)">{{item.tagRuleType | handleTagRuleType}}</div>
                                </div>
                                <div class="tagC">
                                    <div>
                                        <span class="tit">{{item.tagName}}</span>
                                        <span>覆盖量 : </span>
                                        <span class="num"> {{item.keyCount}}</span>
                                        <span class="ms">更新频率：{{item.frequency}} | 最后更新日期：{{item.updateTime}} | 关注人数：{{item.coolection}}</span>
                                    </div>
                                    <div class="hy">
                                        含义：{{item.tagDescription}}
                                    </div>
                                </div>
                            </div>
                            <el-divider></el-divider>
                        </div>
                    </div>
                    <themeLabelList
                        v-if="false"
                        :labelData="labelData"
                        :pageConfig="labelPageConfig"
                        @changePage="changePage"
                        :showDetailData="labelDetailData"
                        @clickDetail="getLabelDetailData"
                        @changeDetailPage="changePage"
                        @exportExcel="themeLabelExportExcel"
                    ></themeLabelList>
                </div>
            </div>
        </div>
        <div class="top">
            <div class="content">
                <div class="content-left content-border">
                    <div class="content-title">
                        <span class="block"></span>
                        <span class="text">标准库地址</span>
                        <el-select size="small" class="input_td" v-model="powerCompany" clearable placeholder="请选择地市局" @change="dsjChange">
                            <el-option
                                v-for="item in powerCompanyOptions"
                                :key="item.ORG_CODE"
                                :label="item.ORG_NAME"
                                :value="item.ORG_CODE"
                            ></el-option>
                        </el-select>
                        <el-select v-model="xqjValue" clearable placeholder="请选择县区局" class="input_td" size="small" @change="qxjChange">
                            <el-option
                                v-for="item in xqjOptions"
                                :key="item.ORG_CODE"
                                :label="item.ORG_NAME"
                                :value="item.ORG_CODE">
                            </el-option>
                        </el-select>
                        <el-select size="small" class="input_td" v-model="gds" clearable placeholder="请选择供电所">
                            <el-option
                                v-for="item in gdsOptions"
                                :key="item.ORG_CODE"
                                :label="item.ORG_NAME"
                                :value="item.ORG_CODE"
                            ></el-option>
                        </el-select>
                        <el-input
                            class="input_td"
                            placeholder="请输入台区名称"
                            size="small"
                            v-model="libValue"
                            clearable>
                        </el-input>
                        <el-button type="primary" size="mini" @click="getStandardAddressDetails('pages')">查询</el-button>
                    </div>
                    <div class="chart-main">
                        <el-table
                            height="330"
                            :data="tableData"
                            v-loading="loadingAddTable"
                            border
                            style="width: 100%">
                            <el-table-column prop="dsj" label="地市局" width="150"></el-table-column>
                            <el-table-column prop="xqj" label="县区局" width="150"></el-table-column>
                            <el-table-column prop="gds" label="供电所" width="150"></el-table-column>
                            <el-table-column prop="tqbs" label="台区标识" width="150"></el-table-column>
                            <el-table-column prop="tqmc" label="台区名称" width="150"></el-table-column>
                            <el-table-column prop="qx" label="区县" width="150"></el-table-column>
                            <el-table-column prop="xz" label="乡镇" width="150"></el-table-column>
<!--                            <el-table-column prop="jwh" label="居委会" width="150"></el-table-column>-->
<!--                            <el-table-column prop="xzc" label="村" width="150"></el-table-column>-->
<!--                            <el-table-column prop="dl" label="道路" width="150"></el-table-column>-->
                        </el-table>
                        <div class="block">
                            <el-pagination
                                @size-change="handleAddSizeChange"
                                @current-change="handleAddCurrentChange"
                                :current-page="pagesAdd.pageNo"
                                :page-sizes="[8, 10, 20, 50]"
                                :page-size="pagesAdd.pageSize"
                                layout="total, sizes, prev, pager, next, jumper"
                                :total="pagesAdd.total">
                            </el-pagination>
                        </div>
                    </div>
                </div>
                <div class="content-right content-border">
                    <div class="content-title">
                        <span class="block"></span>
                        <span>网格热点分布</span>
                    </div>
                    <div class="chart-main">
                        <wordCloud
                            v-if="hotWordRes"
                            :wordCloudData="hotWordData"
                            :name="'热词分布'"
                            @setOption="setOption"
                        ></wordCloud>
                    </div>
                </div>
            </div>
            <!--隐藏台区分布，暂时不用 -->
            <div class="content content-border" v-if="false">
                <div class="content-title">
                    <span class="block"></span>
                    <span>台区分布</span>
                </div>
                <div class="chart-main" v-loading="barLoading">
                    <div ref="barCharts" style="height: calc(100% - 30px);width: 100%"></div>
                    <div class="block">
                        <el-pagination
                            @size-change="barSizeChange"
                            @current-change="barCurrentChange"
                            :current-page="barPages.pageNo"
                            :page-sizes="[6, 10, 20, 50]"
                            :page-size="barPages.pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="barPages.total">
                        </el-pagination>
                    </div>
                </div>
            </div>

            <el-dialog
                title="提示"
                :visible.sync="barTableDialogVisible"
                width="80%">
                <!--            <bar-table ref="bartable"></bar-table>-->
                <div class="barTable" v-loading="barTableLoading">
                    <el-table
                        :data="tqTable"
                        height="250"
                        border
                        stripe
                        style="width: 100%">
                        <el-table-column
                            type="index"
                            label="序号">
                        </el-table-column>
                        <el-table-column
                            prop="tqmc"
                            label="台区名称">
                        </el-table-column>
                        <el-table-column
                            prop="tqbs"
                            label="台区编号">
                        </el-table-column>
                        <el-table-column
                            prop="yhbh"
                            label="客户编号">
                        </el-table-column>
                        <el-table-column
                            prop="yhmc"
                            label="客户名称">
                        </el-table-column>
                        <el-table-column
                            prop="yddz"
                            label="用电地址">
                        </el-table-column>
                        <el-table-column
                            prop="yhlb"
                            label="用户类别">
                        </el-table-column>
                        <el-table-column
                            prop="ydlx"
                            label="用电类型">
                        </el-table-column>
                        <el-table-column
                            prop="gddwbm"
                            label="供电局">
                        </el-table-column>
                        <el-table-column
                            prop="gxsj"
                            label="供电所">
                        </el-table-column>
                    </el-table>
                    <div class="block">
                        <el-pagination
                            @size-change="handleSizeChange"
                            @current-change="handleCurrentChange"
                            :current-page="pages.pageNo"
                            :page-sizes="[8, 10, 20, 50]"
                            :page-size="pages.pageSize"
                            layout="total, sizes, prev, pager, next, jumper"
                            :total="pages.total">
                        </el-pagination>
                    </div>
                </div>
                <span slot="title">
                <span class="table_title">台区明细</span>
                <span class="label">台区：</span>
                    <el-input size="mini" v-model="tqmx" style="width: 260px" placeholder="请输入台区编号或者名称"></el-input>
                    <span class="label" style="margin-left: 20px">客户编号 ：</span>
                    <el-input size="mini" v-model="khbh" style="width: 260px" placeholder="请输入客户编号"></el-input>
                <el-button type="primary" size="mini" style="margin: 0px 10px" @click="getZoneDistributionDetails">查询</el-button>
                <el-button type="primary" size="mini" @click="exportExcel">导出Excel</el-button>
            </span>
                <span slot="footer" class="dialog-footer">
                <el-button @click="barTableDialogVisible = false" size="mini">取 消</el-button>
                <el-button type="primary" @click="barTableDialogVisible = false" size="mini">确 定</el-button>
            </span>
            </el-dialog>
        </div>
    </div>
</template>

<script>
  import Interface from '@/base/mixin/panoramic-application';
  import Interface2 from '@/base/mixin/electricity-behavior';
  import wordCloud from '@/components/word-cloud/word-cloud'
  // import barCharts from '@/components/bar-charts/bar-charts';
  import themeLabelList from '@/components/theme-label-list/theme-label-list'
  import { exportExcelGet, exportExcelPost } from "@/utils/export";
  let option = {
    color: ['#3398DB'],
    tooltip: {
      trigger: 'axis',
      axisPointer: {            // 坐标轴指示器，坐标轴触发有效
        type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
      }
    },
    grid: {
      left: '3%',
      right: '2%',
      bottom: '8%',
      containLabel: true
    },
    xAxis: [
      {
        type: 'category',
        data: [],
        axisTick: {
          alignWithLabel: true,
        },
        axisLabel:{
          // interval: 0,
          rotate:20,//倾斜度 -90 至 90 默认为0
          // margin:2,
          textStyle:{
            fontWeight:"bolder",
            color:"#000000"
          },
          // formatter: function (value) {
          //   //x轴的文字改为竖版显示
          //   var str = value.split("");
          //   return str.join("\n");
          // }
        },
      }
    ],
    yAxis: [
      {
        type: 'value',
        axisTick: {
          show: false  //坐标轴刻度线
        },
        axisLine: { //轴线
          show: false
        },
      }
    ],
    series: [
      {
        name: '数值',
        type: 'bar',
        barMaxWidth: '60',
        data: []
      }
    ]
  }
  export default {
    components: {
      // barCharts,
      themeLabelList,
      wordCloud
    },
    name: "managerPanoramicNew",
    mixins: [Interface,Interface2],
    data() {
      return {
        //搜索条件
        powerCompanyOptions: [],
        powerCompany: '',
        xqjValue:'',
        xqjOptions:[],
        gds:'',
        gdsOptions: [],
        //表头颜色
        headerColor: '#1899fa',
        //关注标签
        labelData: [],

        labelQueryForm: {
          current: 1,
          size: 10,
        },
        //分页设置
        labelPageConfig: {
          name: "label",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
        //标签详情
        labelDetailData: {},
        labelDetailQueryForm: {
          dbcolumnAS: "",
          tagName: "",
          current: 1,
          size: 10,
        },
        //分页设置
        labelDetailPageConfig: {
          name: "labelDetail",
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40, 50],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        },
        //台区分布
        barOption: option,
        //台区分布-点击弹窗
        barTableDialogVisible:false,
        barTableLoading:false,
        tqTable:[],
        pages:{
          pageNo:1,
          pageSize:10,
          total:0,
        },
        //台区分布柱状图分页
        barPages:{
          pageNo:1,
          pageSize:10,
          total:0,
        },
        tqmx:'',
        khbh:'',
        tagListArray:[],
        tagLoading:false,
        //台区分布柱状图分布
        dataX_:[],
        series_:[],
        barLoading:false,

        //热词分布
        hotWordData: {},
        hotWordRes: false,
        //标准地址库
        pagesAdd: {
          pageNo: 1,
          pageSize: 10,
          total: 0,
        },
        loadingAddTable:false,
        libValue:'',
        tableData: []
      }
    },
    watch: {

    },
    filters:{
      handleTagRuleType(v){
        switch (v) {
          case "1":return '规则';
          case "2":return '组合';
          case "3":return '手工';
          case "4":return '模型';
          case "5":return '实时';
          default :break;
        }
      },
    },
    mounted() {
      this.getLabelData()
      this.getZoneDistribution()//台区分布
      this.getHotWordData()//热词
      // this.getStandardAddressDetails() //默认不查询，点击才查询
      this.GetPowerAreaCode()//获取供电单位编码
    },
    methods: {
      /**选择地市局**/
      //获取供电单位编码--地市局
      GetPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "2",//2==供电单位==地市局，3==县区局局，4===供电所
          // "orgCode":"0306" //上一级编码
        }).then(res => {
          this.powerCompanyOptions = res
          this.powerCompany = '0306'
          this.dsjChange()
          this.getStandardAddressDetails()//默认佛山查询
        })
      },
      //获取区县局--区县局
      GetQxjPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "3",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.powerCompany //上一级编码
        }).then(res => {
          this.xqjOptions = res
        })
      },
      //获取供电所
      GetGdsPowerAreaCode() {
        this.getPowerAreaCode({
          "orgLev": "4",//2==供电单位==地市局，3==县区局局，4===供电所
          "orgCode":this.xqjValue //上一级编码
        }).then(res => {
          this.gdsOptions = res
        })
      },
      //地市局选择
      dsjChange(){
        this.gds =''
        this.gdsOptions =[]
        this.xqjValue =''
        this.xqjOptions =[]
        this.GetQxjPowerAreaCode()
      },
      //区县局选择
      qxjChange(){
        this.gds =''
        this.gdsOptions =[]
        this.GetGdsPowerAreaCode()
      },


      //标准化地址
      getStandardAddressDetails(type=''){
        if (type==='pages'){
          this.pagesAdd.pageSize = 10
          this.pagesAdd.pageNo = 1
        }
        this.loadingAddTable = true
        this.standardAddressDetails({
          "dqbm": this.powerCompany,
          "xqjbm": this.xqjValue,
          "gddwbm": this.gds,
          "tqmc": this.libValue,
          "size": this.pagesAdd.pageSize,
          "current": this.pagesAdd.pageNo
        }).then(res=>{
          this.tableData = res.records
          this.pagesAdd.total = res.total
          this.loadingAddTable = false
        })
      },
      handleAddSizeChange(val) {
        this.pagesAdd.pageSize = val
        this.getStandardAddressDetails()
        window.console.log(`每页 ${val} 条`);
      },
      handleAddCurrentChange(val) {
        this.pagesAdd.pageNo = val
        this.getStandardAddressDetails()
        window.console.log(`当前页: ${val}`);
      },
      /**
       *
       * **/
      tagObj(v){
        switch (v) {
          case "1":return 'tag0';
          case "2":return 'tag1';
          case "3":return 'tag2';
          case "4":return 'tag3';
          case "5":return 'tag4';
          default :break;
        }
      },
      //图表组件
      setOption(name, val) {
        this.setOptionData[name] = val
      },
      //获取热词
      getHotWordData() {
        this.hotWordRes = false
        this.gridHotSpotDistribution({}).then(res => {
          this.hotWordData = res.map(e => {
            return {
              name: e.name,
              valueShow: e.count,
            }
          })
          this.hotWordRes = true
          console.log(this.hotWordData, 'hotWordData')
        })
      },
      //改变分页
      changePage(data) {
        if (data.name === 'label') {
          this.labelQueryForm.size = data.size
          this.labelQueryForm.current = data.current
          this.getLabelData()
        }
      },
      //主题标签导出
      themeLabelExportExcel(data) {
        exportExcelGet("/gridManagerPanoramaManagerController/exportUserDetailsToTags" + "?tagName=" + this.labelDetailQueryForm.tagName);
      },
      //客户明细列表导出
      detailListExportExcel() {
        console.log('111')
        exportExcelPost("/gridManagerPanoramaManagerController/exportCourtsExcel", {
          courtsNumber: this.customerCoverageBarQueryForm.courtsNumber,
          customerNumbers: this.customerCoverageBarQueryForm.customerNumbers,
        });

      },
      // 获取关注标签
      getLabelData() {
        this.tagLoading =true
        this.payAttentionTags(this.labelQueryForm).then(res => {
          this.tagLoading = false
          this.tagListArray = res
          // this.labelPageConfig.total = res.total
          // this.labelPageConfig.size = this.labelQueryForm.size
          // this.labelPageConfig.current = this.labelQueryForm.current
          // this.labelData = res.list.map(e => {
          //   return {
          //     ...e,
          //     name: e.bqmc,
          //     text: e.bqsm,
          //     value: e.sl || 0,
          //   }
          // })


        })
      },
      //获取标签详情信息
      getLabelDetailData(data) {
        if (data) {
          this.labelDetailQueryForm.tagName = data.name
          this.labelDetailQueryForm.size = 10
          this.labelDetailQueryForm.current = 1
        }
        let columnNameData = [
          {prop: "yhbh", label: "用户编号"},
          {prop: "khbh", label: "客户编号"},
          {prop: "yhmc", label: "用户名称"},
          {prop: "yddz", label: "用电地址"},
          {prop: "yhlb", label: "用户类别"},
          {prop: "ydlb", label: "用电类别"},
          {prop: "sfzdgz", label: "是否重点关注客户"},
          {prop: "sfxh", label: "是否现货客户"},
          {prop: "dq", label: "地区"},
          {prop: "qxj", label: "县区局"},
          {prop: "gddw", label: "供电单位"},
          {prop: "cjsj", label: "创建时间"},
          {prop: "gxsj", label: "更新时间"},
        ]
        this.userDetailsToTags({...this.labelDetailQueryForm}).then(res => {
          this.labelDetailPageConfig.total = res.total
          this.labelDetailPageConfig.size = this.labelDetailQueryForm.size
          this.labelDetailPageConfig.current = this.labelDetailQueryForm.current
          this.labelDetailData = {
            tableData: [...res.list],
            columnNameData: columnNameData,
            name: "labelDetail",
            headerColor: '#1899fa',
            title: "客户明细列表",
            total: this.labelDetailPageConfig.total,
            current: this.labelDetailQueryForm.current,
            size: this.labelDetailQueryForm.size,

          }
          console.log(this.labelDetailData, 'labelDetailData')
        })
      },

      //台区分布-调用台区分布接口调用数据
      getZoneDistribution(){
        this.barLoading = true;
        this.zoneDistribution({
          "current":this.barPages.pageNo,
          "size":this.barPages.pageSize,
        }).then(data=>{
          this.barLoading = false;
          let dataX = []
          let series = []
          let res = data.records
          for(let i =0;i<res.length;i++){
            dataX.push(res[i].tqmc)
            series.push(res[i].total)
          }
          this.barPages.total = data.total;
          this.dataX_ = dataX
          this.series_ = series
          this.init()
        })
      },

      //台区分布详情
      getZoneDistributionDetails(){
        this.barTableLoading = true,
        this.barTableDialogVisible = true,
        this.zoneDistributionDetails({
          "current":this.pages.pageNo,
          "size":this.pages.pageSize,
          "khbh":this.khbh,
          "tq":this.tqmx,
        }).then(res=>{
          this.pages.total =res.total;
          this.tqTable = res.records
          this.barTableLoading = false
        })
      },
      barSizeChange(val){
        this.barPages.pageSize = val
        this.getZoneDistribution()
      },
      barCurrentChange(val){
        this.barPages.pageNo = val
        this.getZoneDistribution()
      },

      //台区分布初始化charts
      init() {
        this.barOption.xAxis[0].data = this.dataX_
        this.barOption.series[0].data = this.series_
        let echarts = require('echarts');
        this.mychart = echarts.init(this.$refs.barCharts)
        this.mychart.setOption(this.barOption,true);
        let mychart = echarts.init(this.$refs.barCharts, true)  //监听窗口变化重新加载x
        window.addEventListener("resize", mychart.resize); //监听窗口变化重新加载

        //点击事件
        let _this = this
        this.mychart.off('click');
        this.mychart.on('click', function (params) {
          _this.getZoneDistributionDetails(params)
          _this.tqmx = params.name
          //获取业务渠道下拉
         window.console.log(params)
        })
      },
      handleSizeChange(val) {
        this.pages.pageSize = val
        this.getZoneDistributionDetails()
        console.log(`每页 ${val} 条`);
      },
      handleCurrentChange(val) {
        this.pages.pageNo = val
        this.getZoneDistributionDetails()
        console.log(`当前页: ${val}`);
      },
      exportExcel(){ //导出

      }
    }
  }
</script>

<style scoped lang="scss">
    ::v-deep .el-table .warning-row {
        // background: #e7f6f7;
        background: #e9f3fe;
    }
    ::v-deep .el-table {
        .success-row {
            background: rgb(244, 244, 244);
        }
        .cell {
            padding: 2px 4px;
        }
        th {
            background-color: rgb(24, 153, 250);
            .cell {
                color: #ffffff;
            }
        }
    }
    .input_td{
        width: calc(20% - 4px);
        margin-left: 4px;
    }
    .managerPanoramicNew {
        width: 100%;
        height: 100%;
        font-size: 14px;
        box-sizing: border-box;
        .table_title{
            font-weight: bold;
            color: #1A84F4;
            margin-right: 20px;
        }
        .top,
        .center,
        .top-bottom,
        .bottom {
            width: 100%;
            // height: calc(50% - 10px);
            height: 430px;
            box-sizing: border-box;
            margin-bottom: 20px;

            .content-border {
                background: #fff;
                border: 2px solid rgba(26, 132, 244, 0.2);
                border-radius: 4px;
            }
            .content {
