<!--气泡词云图-->
<template>
  <div class="word-cloud-style">
    <div :id="id" class="charts"></div>
  </div>
</template>

<script>
/**
 * @气泡词云图
 * @author：risheng.huang
 * @参数说明：
 * wordCloudData{
 *     dataMap:[name:"",value:''],    /对应的数据值(百分比)
 *     colorMap:['#31fff1','#27ccc1'], /颜色渐变
 * }  

 * */
import echarts from "echarts";

export default {
  name: "word-cloud",
  props: {
    wordCloudData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    name: {
      type: String,
      default: () => {
        return "";
      }
    }
  },
  data() {
    return {
      id: Math.random() + "_",
      dealData: []
    };
  },
  watch: {},
  created() {},
  mounted() {
    this.init();
  },
  methods: {
    dataFilter(data) {},
    init() {
      let chartsDom = document.getElementById(this.id);
      let charts = echarts.init(chartsDom);
      let options = {};
      let data = this.wordCloudData;
      console.log(data, "data");
      let color = [
        { itemColor: "#fdf1e6", labelColor: "#F37508" },
        { itemColor: "#e6f8fd", labelColor: "#08BEF3" },
        { itemColor: "#eaf9ec", labelColor: "#2DC746" },
        { itemColor: "#e8f2fd", labelColor: "#1A84F4" },
        { itemColor: "#f0f9ee", labelColor: "#6cbf53" },
        { itemColor: "#eae7fd", labelColor: "#8a7cec" }
      ];

      let dataLength = data.length;
      let colorLength = color.length;
      color = this.fillData(color, dataLength, colorLength);
      //偏移量
      let offsetData =
        document.body.clientWidth > 1600
          ? [
              [0, 47],
              [6, 24],
              [8, 64],
              [14, 38],
              [22, 23],
              [20, 63],
              [30, 27],
              [28, 64],
              [36, 60],
              [40, 22]
            ]
          : [
              [0, 40],
              [9, 60],
              [14, 20],
              [20, 55],
              [30, 64],
              [34, 25]
            ];
      this.randomSort(offsetData);
      this.randomSort(data);
      this.randomSort(color);
      //symbolSize 散点气泡大小
      let symbolSizeData = [80, 100, 120];
      //
      //循环定义series的data值
      let datas = [];
      for (let i = 0; i < data.length; i++) {
        let item = data[i];
        //let itemToStyle = datalist[i];
        let num = item.valueShow >= 100 ? 20 : 16;
        let textColor = item.valueShow >= 100 ? "#d97979" : "#808080";
        datas.push({
          name: item.name,
          value: offsetData[i],
          valueShow: item.valueShow,
          // symbolSize: symbolSizeData[Math.floor((Math.random() * symbolSizeData.length))],
          symbolSize:
            item.symbolSize ||
            symbolSizeData[Math.floor(Math.random() * symbolSizeData.length)],
          itemStyle: {
            color: color[i].itemColor
          },
          label: {
            formatter: function(params) {
              let name = "";
              if (params.name.length > 4) {
                name = params.name.slice(0, 4) + "…";
              } else {
                name = params.name;
              }
              // let name1 = params.name.substring(0, 6)
              // let name2 = params.name.substring(6, 12)
              // return name2 ? ['{a|' + params.percent + '%}', '{b|' + name1 + '}', '{c|' + name2 + '}'].join('\n') : ['{a|' + params.percent + '%}', '{b|' + name1 + '}'].join('\n');
              return [
                "{a|" + name + "}",
                "{b|" + params.data.valueShow + "次}"
              ].join("\n");
            },
            textStyle: {
              //在rich中给formatter添加个别字体颜色
              rich: {
                a: {
                  fontSize: 14,
                  align: "center",
                  fontWeight: "bold",
                  padding: [10, 0, 0, 0],
                  color: color[i].labelColor
                },
                b: {
                  fontSize: num,
                  align: "center",
                  color: textColor
                }
              }
            }
          },
          emphasis: {
            itemStyle: {
              color: color[i].itemColor
            }
          }
        });
      }
      options = {
        backgroundColor: "transparent",
        hoverAnimation: false,
        grid: {
          show: false,
          top: 10,
          bottom: 10
        },
        xAxis: [
          {
            show: false
          }
        ],
        yAxis: [
          {
            show: false,
            max: 80
          }
        ],
        series: [
          {
            type: "scatter",
            symbol: "circle",
            symbolSize: 120,
            label: {
              normal: {
                show: true
              }
            },
            data: datas
          }
        ]
      };

      charts.setOption(options);
      this.$emit("setOption", this.name, charts);
    },
    //随机排序
    randomSort(data) {
      data.sort(function() {
        return Math.random() - 0.5;
      });
    },
    //补全数据长度
    fillData(data, dataLength, baseLength) {
      let dataDeal = [...data];
      if (dataLength > baseLength) {
        for (let i = 0; i < dataLength - baseLength; i++) {
          if (baseLength === 1) {
            dataDeal.push(JSON.parse(JSON.stringify(dataDeal[0])));
          } else {
            dataDeal.push(
              JSON.parse(
                JSON.stringify(
                  dataDeal[i - baseLength * Math.floor(i / baseLength)]
                )
              )
            );
          }
        }
      }
      return dataDeal;
    }
  }
};
</script>

<style scoped lang="scss">
.word-cloud-style {
  width: 100%;
  height: 100%;
  position: relative;

  .charts {
    width: 100%;
    height: 100%;
  }
}
</style>