<!-- 点击柱状图展示详情 -->
<template>
  <div class="lable-table">
    <div class="lable-header">
      <span class="name">标签名称</span>
      <span class="text">标签说明</span>
      <span class="value">覆盖客户数</span>
    </div>
    <div class="lable-box" :style="{height:tableHeight+'px'}" v-if="labelDealData.length!==0">
      <div class="lable-item" v-for="(item, index) in labelDealData" :key="index">
        <span class="name" :style="{ backgroundColor: item.color }">
          {{
          item.name
          }}
        </span>
        <span class="text" :style="{ backgroundColor: item.backgroundColor }">
          {{
          item.text
          }}
        </span>
        <span
          class="value"
          :style="{ backgroundColor: item.backgroundColor }"
          @click="showDetail(item)"
        >{{ item.value + "户" }}</span>
      </div>
    </div>
    <div v-else class="no-data">
      <span>暂无数据</span>
    </div>
    <el-pagination
      v-if="isPage"
      class="content-pagination"
      @size-change="sizeChange"
      @current-change="currentChange"
      :current-page="queryForm.current"
      :page-sizes="pageConfig.pageSizes"
      :page-size="queryForm.size"
      :layout="pageConfig.layout"
      :total="pageConfig.total"
    ></el-pagination>
    <showDetailList
      v-if="showDetailVisible"
      :visible.sync="showDetailVisible"
      :detailData="detailData"
      :isQuery="isQuery"
      @changePageData="changePageData"
      @exportExcel="exportExcel"
      ref="showDetailList"
    ></showDetailList>
  </div>
</template>

<script>
// import Interface from '../base/mixin/talk-manage';
// import bdaTable from '../bda-table/bda-table'
import showDetailList from '@/components/show-detail-list/show-detail-list'


export default {
  components: {
    showDetailList
  },
  props: {
    labelData: {
      type: Array,
      default: () => {
        return []
      },
    },
    showDetailData: {
      type: Object,
      default: () => {
        return {

        }
      }
    },
    isQuery: {
      type: Boolean,
      default: () => {
        return false
      }
    },
    isPage: {
      type: Boolean,
      default: () => {
        return true
      }
    },
    pageConfig: {
      type: Object,
      default: () => {
        return {
          name: '',
          current: 1,
          size: 10,
          pageSizes: [10, 20, 30, 40],
          layout: "prev, pager, next, sizes,jumper,total",
          total: 0
        }
      }
    },
  },
  name: "show-detail-list",
  //   mixins: [Interface],
  data() {
    return {
      tableHeight: 0,
      //查询
      queryForm: {
        tagId: "",
        name: '',
        current: 1,
        size: 10,
      },
      //标签列表
      labelDealData: [],
      //客户明细表
      showDetailVisible: false,
      detailData: {},
      //当前行数据
      rowData: {},
      //查询条件
      detailQueryForm: {
        current: 1,
        size: 10,
      },
      //分页设置
      detailPageConfig: {
        name: "detail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
    }
  },
  mounted() {
    this.init()
  },
  watch: {
    labelData() {
      this.dataDeal(this.labelData)
    },
    showDetailData() {
      console.log(this.showDetailData, 'showDetailData')

      if (Object.keys(this.showDetailData).length !== 0) {
        this.showDetailDeal()
      }
    }
  },
  methods: {
    changeLoading(){
      this.$refs.showDetailList.themeLoading = false
    },
    // 初始化数据
    init() {
      this.tableHeight = document.getElementsByClassName('lable-table')[0].clientHeight - 30 - (this.isPage ? 52 : 0)
      this.queryForm.name = this.pageConfig.name
      this.queryForm.size = this.pageConfig.size
      this.queryForm.current = this.pageConfig.current
      this.dataDeal(this.labelData)
    },
    //条数改变
    sizeChange(val) {
      this.queryForm.size = val;
      this.$emit('changePage', this.queryForm)
    },
    //页数改变
    currentChange(val) {
      this.queryForm.current = val;
      this.$emit('changePage', this.queryForm)
    },
    //导出
    exportExcel(data) {
      this.$emit('exportExcel', { ...data, tagId: this.queryForm.tagId })
    },
    //展示详情
    showDetail(data) {
      // this.rowData = data
      // console.log(data, 'rowData')
      this.queryForm.tagId = data.tagId
      this.$emit('clickDetail', data)
    },
    //展示详情
    changePageData(data) {
      this.rowData = data
      this.$emit('changeDetailPage', { ...data, ... this.rowData })
    },
    //详情数据
    showDetailDeal() {
      let data = { ...this.showDetailData }
      console.log(data, 'rowData')

      this.detailData = data
      this.showDetailVisible = true
    },
    // 数据处理
    dataDeal(data) {
      this.labelDealData = []
      if (Object.keys(data).length !== 0) {
        let colorList = ['#3890FF', '#21CCA8', '#E69617', '#1ABDEF', '#546EDD', '#1FB7C8', '#8663D5']
        let backgroundColorList = ['#ebf3ff', '#e8f9f6', '#fcf4e7', '#e8f8fd', '#edf0fb', '#e8f7f9', '#f2effa']
        data.forEach((e, i) => {
          if (i < colorList.length) {
            e.color = colorList[i]
            e.backgroundColor = backgroundColorList[i]
          } else {
            e.color = colorList[i - colorList.length * Math.floor(i / colorList.length)]
            e.backgroundColor = backgroundColorList[i - colorList.length * Math.floor(i / colorList.length)]
          }
        });
        this.$nextTick(() => {

          this.labelDealData = data
        })
      }

    },
  },
}
</script>

<style scoped lang="scss">
.lable-table {
  width: 100%;
  height: calc(100% - 10px);
  border: 1px solid #e6e6e6;
  border-radius: 4px;
  font-size: 12px;
}
.lable-box {
  width: 100%;
  height: calc(100% - 30px - 52px);
  overflow-y: auto;
}
.no-data {
  width: 100%;
  height: calc(100% - 30px - 52px);
  display: flex;
  justify-content: center;
  align-items: center;
  color: #909399;
}
.content-pagination {
  margin: 10px 0;
  text-align: center;
}
::v-deep .el-pagination__total {
  margin-left: 10px;
}
.name {
  width: 25%;
  // display: flex;
  // align-items: center;
  // text-indent: 2em;
  line-height: 30px;

  padding-left: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.text {
  // width: calc(100% - 160px - 100px);
  width: calc(100% - 25% - 20% - 20px);
  line-height: 30px;
  // text-indent: 20px;
  padding-left: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.value {
  width: 25%;
  height: 30px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.lable-header {
  width: 100%;
  height: 30px;
  display: flex;
  margin-bottom: 2px;
  color: #4d4d4d;
  font-weight: bold;
}
.lable-item {
  width: 100%;
  height: 30px;
  line-height: 30px;
  display: flex;
  margin-bottom: 2px;
  .name {
    color: #ffffff;
    font-weight: bold;
  }
  .text {
    color: #4d4d4d;
  }
  .value {
    color: #1a84f4;
    cursor: pointer;
  }
}
</style>